<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Nueva Persona"
    >
      <v-card>
        <v-card-title>
          <!-- <span class="text-h4"><b>{{ formTitulo }}</b></span> -->
        </v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="valid">
            <!-- {{persona}} -->
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.ci"
                    label="Carnet Identidad"
                    hint="Solo numero, sin extensin"
                    :counter="10"
                    :rules="fieldRules"
                    :autofocus="true"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.paterno"
                    label="Apellido Paterno"
                    hint="Introducir ap. paterno"
                    :counter="50"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.materno"
                    label="Apellido Materno"
                    hint="Introducir ap. materno"
                    :counter="50"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.nombre"
                    label="Nombre(s)"
                    hint="Introducir nombre"
                    :counter="50"
                    :rules="fieldRules"
                    persistent-hint
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.apellidoEsposo"
                    label="Apellido Esposo"
                    hint="Llenar si corresponde"
                    :counter="50"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.nacionalidad"
                    label="Nacionalidad"
                    hint="Introducir nacionalidad"
                    :counter="50"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-text-field
                    v-model="persona.lugarNacimiento"
                    label="Lugar Nacimiento"
                    hint="Ciudad o Provincia"
                    :counter="50"
                    persistent-hint
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="3"
                >
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="persona.fechaNacimiento"
                        label="Fecha de nacimiento"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="persona.fechaNacimiento"
                      @input="menu = false"
                    >
                    </v-date-picker>
                  </v-menu>
                </v-col>
              </v-row>



              <v-row>
                <v-col
                  cols="12"
                  sm="6"
                  md="4"
                >
                  <v-autocomplete
                    v-model="persona.estadoCivil"
                    :items="lista_estadocivil"
                    item-text="estadoCivil"
                    item-value="estadoCivil"
                    label="Estado Civil"
                    hint="Seleccione el estado civil"
                    persistent-hint
                    no-data-text="No existen datos"
                  >
                  </v-autocomplete>
                </v-col>
              </v-row> 
              
              
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            text
            @click="close"
            :to="'/tables/persona'"
          >
            Cancelar
          </v-btn>
          <v-btn
            :loading="estadoBoton"
            @click="guardar"
            :disabled="!valid"
            color="primary"
          >
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>

    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>


<script>
import * as personaService from "../services/personaService"

  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        lista_persona: [],
        lista_estadocivil: [
          'Soltero(a)', 'Casado(a)', 'Divorciado(a)', 'Viudo(a)'
        ],
        cargando: true,
        persona: {
        },
        search: '',
        editedIndex: -1,
        defaultPersona: {
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu: false,
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
      
      
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      
    },
    async mounted () {
      this.obtener()
    },
    methods: {

      getItemText(item) {
        return `${item.first_value} ${item.second_value}`;
      },

      async obtener () {
        const res = await personaService.listar()
        // console.log(res)
        this.lista_persona = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.persona = Object.assign({}, this.defaultPersona)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        this.persona = Object.assign(this.persona);
        console.log(this.persona);
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mod = this.lista_persona[this.editedIndex]
          res = await personaService.modificar(this.persona, mod.id)
          Object.assign(this.lista_persona[this.editedIndex], this.persona)
        } else {
          res = await personaService.guardar(this.persona)
          this.lista_persona.push(this.persona)
          this.$router.push({ name: "Persona" })
          // window.location.href = '/tables/persona'
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_persona.indexOf(item)
        this.persona = Object.assign({}, item)
        this.dialog = true
      },
      validate() {
        this.$refs.form.validate()
      }
    },
  }
</script>
